
nav {
  z-index: 100;
  background-color: #3f51b5 ;
}
.nav-link {
  opacity: 0.6;
  transition: 0.3s;
}
.nav-link:hover {
  opacity: 1;
  transition: 0.3s;
}
.nav-link.router-link-active {
  opacity: 1;
  transition: 0.3s;
}
#logo
{
  margin: 10px;
  height: 45px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url("../assets/logo_300.png");
  width: 45px;
}



